import React, { useState } from 'react';

const Token = () => {
    
    return (
        <div className="half_container">
            <h2 className="sub_title">Connect</h2>
            <div className="token_div">
                <p>Token : </p>
                <p className="token" id="token"></p>
                <button id="electron_start">Start</button>
            </div>
        </div>
    );
}

export default Token